import Main from '@/views/layout/Main.vue';

// Main
export const loginRouter = {
  path: '/login',
  name: 'login',
  meta: {
    title: 'Login - '
  },
  component: () =>
    import ('@/views/auth/Login.vue')
};

export const page404 = {
  path: '/*',
  name: 'error-404',
  meta: {
    title: '404-'
  },
  component: () =>
    import ('@/views/error-page/404.vue')
};

export const page403 = {
  path: '/403',
  meta: {
    title: '403-'
  },
  name: 'error-403',
  component: () =>
    import ('@/views/error-page/403.vue')
};

export const page500 = {
  path: '/500',
  meta: {
    title: '500-'
  },
  name: 'error-500',
  component: () =>
    import ('@/views/error-page/500.vue')
};

export const locking = {
  path: '/locking',
  name: 'locking',
  component: () =>
    import ('@/views/other/LockingPage.vue')
};

// MainotherRouter
export const otherRouter = {
  path: '/',
  name: 'otherRouter',
  redirect: '/login',
  component: Main,
  children: [{
      path: 'home',
      title: { i18n: 'home' },
      name: 'home_index',
      component: () =>
        import ('@/views/home/Home.vue')
    },
    {
      path: 'ownspace',
      title: '',
      name: 'ownspace_index',
      component: () =>
        import ('@/views/own-space/OwnSpace.vue')
    },
    {
      path: 'message',
      title: '',
      name: 'message_index',
      component: () =>
        import ('@/views/message/Message.vue')
    }
  ]
};

